import { startandendsType } from 'calm-harbin/types/utils'
/**
 * 
 * @param dayNum 
 *
 * ```
 *   - 
 * : startandends(30) => [new Date('2021/6/24 00:00:00'), new Date('2021/7/24 23:59:59')]
 * ```
 */
export const startandends: startandendsType = (dayNum) => {
    const date = new Date()

    const end = new Date(
        date.getFullYear(),
        date.getMonth(),
        date.getDate(),
        23,
        59,
        59
    )
    const start = new Date(
        date.getFullYear(),
        date.getMonth(),
        date.getDate(),
        0,
        0,
        0
    )
    start.setTime(start.getTime() - 3600 * 1000 * 24 * dayNum)
    return [start, end]
}
